<template>
  <div class="common-detail">
    <div class="content">
      <Form :label-width="120">
          <FormItem label="基于反洗钱机制成功防范的风险事件名称：">
            <label>{{watchTableCell.val1}}</label>
          </FormItem>
          <FormItem label="事件发起机构：">
            <label>{{watchTableCell.val2}}</label>
          </FormItem>
          <FormItem label="事发时间：">
            <label>{{watchTableCell.val3}}~{{watchTableCell.val4}}</label>
          </FormItem>
          <FormItem label="基本情况（隐去当事人和机构名称）：">
            <label>
              <!-- {{watchTableCell.val5}} -->
                <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val5"
                  >
                    <p slot="reference" class="textMaxWidth2 disBlock textOverflow" >{{watchTableCell.val5}}</p>
                  </el-popover>
              </label>
          </FormItem>
          <FormItem label="取得成果：">
            <label>{{watchTableCell.val6}}</label>
          </FormItem>
          <FormItem label="是否向人行或当地人行机构报备：">
            <label>{{watchTableCell.val7?'是':'否'}}</label>
          </FormItem>
          <FormItem label="是否公开：">
            <label>{{watchTableCell.val8?'是':'否'}}</label>
          </FormItem>
          <FormItem label="编辑内容：">
            <label>
              <!-- {{watchTableCell.val9}} -->
              <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val9"
                  >
                    <p slot="reference" class="textMaxWidth2 disBlock textOverflow" >{{watchTableCell.val9}}</p>
                  </el-popover>
              </label>
          </FormItem>
          <FormItem label="附件：">
            <showUsualFiles
              :file-obj="dataObj4"
              :key="fileId4"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加附件：">
            <showUsualFiles
              :file-obj="dataObj5"
              :key="fileId5"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加内容：">
            <label>
              <!-- {{watchTableCell.val11}} -->
                 <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val11"
                  >
                    <p slot="reference" class="textMaxWidth2 disBlock textOverflow" >{{watchTableCell.val11}}</p>
                  </el-popover>
              </label>
          </FormItem>
        </Form>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      addPeopleArr: [], //被调查人
      watchTableCell: {
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: ""
      },
      fileId4: "",
      fileId5: "",
      dataObj4: {},
      dataObj5: {}
    };
  },
  created() {
    let data = this.dataObj.data;
    if (data) {
      this.watchTableCellFun(data, data.busId, data.id);
    }
  },
  methods: {
    watchTableCellFun(val1, val2, val3) {
      this.axios
        .post(`/process/infRiskEvents/getEntityRow/${val3}`+'?infViewNum=1')
        .then(response => {
          if (response.code == "200") {
            this.watchTableCell.val1 = response.data.eventName;
            this.watchTableCell.val2 = response.data.launchOrg;
            this.watchTableCell.val3 = response.data.eventStartTime;
            this.watchTableCell.val4 = response.data.eventEndTime;
            this.watchTableCell.val5 = response.data.baseContent;
            this.watchTableCell.val6 = response.data.result	;
            this.watchTableCell.val7 = JSON.parse(response.data.ifReportBank);
            this.watchTableCell.val8 = response.data.ifShow;
            this.watchTableCell.val9 = response.data.content;
            this.dataObj4 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId4 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: val2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId5 = !this.fileId5;
          }
        })
        .catch(function(error) {});
      // ---------------
    },
  }
};
</script>


